    font-family: 'Raleway', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f0f0f0;
  }
  
  .content {
    position: relative;
    width: 320px;
    height: auto;
  }
  
  .content > div {
    position: absolute;
    /* width: 320px;*/
    height: 180px; 
    overflow: visible;
    pointer-events: auto;
    transform-origin: 50% 50% 0px;
    border-radius: 15px;
